<template>
  <div>
    <Modal v-model="showDel" width="360" :mask-closable="false" :closable="false">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="ios-information-circle"></Icon>
        <span>是否删除该条数据？</span>
      </p>
      <div style="text-align:center">
        <p>删除数据后将无法恢复</p>
      </div>
      <div slot="footer" style="text-align:center;">
        <Button type="error" @click="del">删除</Button>
        <Button style="margin-left:40px;" @click="cancelDel">取消</Button>
      </div>
    </Modal>

    <Modal
      v-model="modelEdit"
      width="700"
      :mask-closable="false"
      :closable="false"
      :transfer="false"
    >
      <p slot="header">
        <span style="font-size:20px">话术详情</span>
      </p>

      <div style="margin-bottom:10px">
        <Button style="displa:inline-block" type="primary" @click="addText">新增文本</Button>
        <Button v-if="isShowTask" style="displa:inline-block" type="primary" @click="addTask">新增任务</Button>
        <Button v-if="!isShowTask" style="displa:inline-block" disabled type="primary">新增任务</Button>
      </div>

      <div>
        <div
          style="background-color:rgba(235, 240, 251, 1);width:638px;height:120px;margin-bottom:10px"
          v-for="(item,index) in editNode.nodeTextList"
          :key="index"
        >
          <span
            style="color:#07175D;display:inline-block;text-align:left;margin-left:10px;margin-top:10px"
          >话术文本</span>
          <a
            style="color:red;display:inline-block;float:right;margin-right:10px;margin-top:10px"
            @click="deleteText1(index)"
          >删除</a>
          <Input
            type="textarea"
            :rows="3"
            placeholder="输入话术文本"
            v-model="item.content"
            style="width:620px;height:78px;margin-left:10px;margin-top:5px"
          ></Input>
        </div>
      </div>

      <br />
      <div v-if="isShowTaskEdit">
        <div
          style="background-color:rgba(235, 240, 251, 1);width:638px;height:168px;margin-bottom:10px"
        >
          <span
            style="color:#07175D;display:inline-block;text-align:left;margin-left:10px;margin-top:10px"
          >任务资源</span>
          <a
            style="color:red;display:inline-block;float:right;margin-right:10px;margin-top:10px"
            @click="deleteTask1"
          >删除</a>
          <div style="margin-top:10px">
            <span
              style="color:#07175D;display:inline-block;text-align:left;margin-left:10px;margin-top:10px"
            >推荐资源</span>
            <Select
              v-model="editNode.taskResourceMap.type"
              @on-change="select2"
              style="width: 70px"
            >
              <Option
                v-for="(element,index2) in resourceType"
                :value="element.type"
                :key="index2"
              >{{element.value}}</Option>
            </Select>
            <Select v-model="editNode.taskResourceMap.contentId" style="width: 490px">
              <Option
                v-for="(element1,index3) in contentList"
                :key="index3"
                :value="element1.contentId"
              >{{element1.title}}</Option>
            </Select>
          </div>
          <Checkbox
            v-model="editNode.taskResourceMap.isComment"
            style="margin-left:10px;margin-top:10px"
          >接受用户评价</Checkbox>
        </div>
      </div>

      <br />
      <div>
        <div style="width:57px;height:20px">
          <p>
            <span style="color: #07175D;font-size: 14px;">触发条件</span>
          </p>
        </div>
        <br />
        <div>
          <Select v-model="editNode.term" style="width:100px;display:inline-block">
            <Option value="0">无</Option>
            <Option value="1">用户选择</Option>
            <Option value="2">用户输入</Option>
          </Select>
          <Input
            style="width:56px;display:inline-block;text-align:center"
            type="text"
            placeholder="请输入内容"
            v-model="editNode.termValue"
            v-if="editNode.term&&editNode.term!=0"
          ></Input>
        </div>
      </div>
      <div slot="footer">
        <Button type="error" style="float:left;" @click="toDel">删除</Button>
        <Button @click="cancel">取消</Button>
        <Button type="primary" @click="save">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
export default {
  data() {
    return {
      showDel: false,
      isShowTask: false,
      nodeId: "",
      isShowTaskEdit: false,
      modelEdit: true,
      editNode1: {},
      editNode: {
        nodeId: "",
        term: "0",
        termValue: "",
        nodeTextList: [],
        taskResourceMap: {
          isComment: 2
        }
      },
      resourceType: [
        {
          type: 1,
          value: "音频"
        },
        {
          type: 2,
          value: "视频"
        },
        {
          type: 3,
          value: "电子书"
        },
        {
          type: 4,
          value: "问卷"
        },
        {
          type: 5,
          value: "表单"
        },
        {
          type: 6,
          value: "小游戏"
        }
      ],
      contentList: []
    };
  },
  methods: {
    getDetail() {
      this.$axios
        .post("api/verbalTrickNode/selectVerbalTrickNode", {
          nodeId: this.nodeId
        })
        .then(res => {
          this.editNode1 = res.data.data;
          this.editNode.nodeId = this.nodeId;
          this.editNode.upId = this.editNode1.upId;
          if(this.editNode1.term){
            this.editNode.term = this.editNode1.term.toString();
          }
          this.editNode.termValue = this.editNode1.termValue;
          if (this.editNode1.nodeTextList) {
            this.editNode1.nodeTextList.forEach(item => {
              this.editNode.nodeTextList.push({ content: item.content });
            });
          }
          if (JSON.stringify(this.editNode1.taskResourceMap) == "{}") {
            this.isShowTask = true;
            this.editNode.taskResourceMap = {};
          } else {
            this.isShowTaskEdit = true;
            this.editNode.taskResourceMap = this.editNode1.taskResourceMap;
            if (this.editNode1.taskResourceMap.isComment == 1) {
              this.editNode.taskResourceMap.isComment = true;
            }
            if (this.editNode1.taskResourceMap.isComment == 2) {
              this.editNode.taskResourceMap.isComment = false;
            }
            this.select2(this.editNode1.taskResourceMap.type);
          }
        });
    },
    addText() {
      this.editNode.nodeTextList.push({ content: "" });
    },
    addTask() {
      this.editNode.taskResourceMap = {
        isComment: 2,
        type: 1
      };
      this.$axios
        .post("api/taskResource/getTaskResourceList", {
          type: this.editNode.taskResourceMap.type
        })
        .then(res => {
          this.contentList = res.data.data;
          this.editNode.taskResourceMap.contentId = this.contentList[0].contentId;
        });
      this.isShowTaskEdit = true;
      this.isShowTask2 = false;
    },
    deleteTask1() {
      this.editNode.taskResourceMap = {};
      this.isShowTaskEdit = false;
      this.isShowTask = true;
    },
    select2(e) {
      this.editNode.taskResourceMap.type = e;
      this.$axios
        .post("api/taskResource/getTaskResourceList", { type: e })
        .then(res => {
          console.log(res);
          this.contentList = res.data.data;
        });
    },
    deleteText1(index1) {
      this.editNode.nodeTextList.splice(index1, 1);
    },
    save() {
      let editNewNode = {
        taskResourceMap: {}
      };
      editNewNode.nodeId = this.editNode.nodeId;
      if (
        this.editNode.term &&
        this.editNode.termValue &&
        this.editNode.term != 0
      ) {
        editNewNode.term = parseInt(this.editNode.term);
        editNewNode.termValue = this.editNode.termValue;
      }
      editNewNode.nodeTextList = this.editNode.nodeTextList;
      if (JSON.stringify(this.editNode.taskResourceMap) != "{}") {
        editNewNode.taskResourceMap = this.editNode.taskResourceMap;
        if (this.editNode.taskResourceMap.isComment == true) {
          this.editNode.taskResourceMap.isComment = 1;
        }
        if (this.editNode.taskResourceMap.isComment == false) {
          this.editNode.taskResourceMap.isComment = 2;
        }
      } else {
        delete editNewNode.taskResourceMap;
      }
      this.$axios
        .post("api/verbalTrickNode/editVerbalTrickNode", editNewNode)
        .then(res => {
          console.log(res);
          this.$router.go(-1);
        });
    },
    toDel() {
      this.showDel = true;
    },
    del() {
      this.$axios
        .post("api/verbalTrickNode/deleteVerbalTrickNode", {
          nodeId: this.nodeId
        })
        .then(res => {
          if (res.data.code == 0) {
            this.$router.go(-1);
            this.$Message.success("删除成功！");
          } else {
            this.$Message.error(res.data.msg);
          }
        });
    },
    cancel() {
      this.$router.go(-1);
    },
    cancelDel() {
      this.showDel = false;
    }
  },
  mounted() {
    this.nodeId = this.$route.query.id;
    this.getDetail();
  }
};
</script>


